import React from "react";

import "./Carousel.css";
import CarouselItem from "./CarouselItem";
import AuctionInfoBox from "./AuctionInfoBox";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faChevronLeft, faChevronCircleRight, faChevronCircleLeft } from '@fortawesome/free-solid-svg-icons';

const Carousel = props => { 


    return (
        <div id="auctionsCarousel" className="carousel slide" data-ride="carousel">
            <div className="carousel-inner">
                <CarouselItem className="active">
                    <img src="assets/images/Info-Board_1200x1200.jpg" className=" carousel-image d-block w-100" alt="..." />
                    <AuctionInfoBox >
                        <h1>Shipping Info</h1>
                        <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Provident nobis recusandae est aspernatur, ratione explicabo laborum ex neque corporis totam.</p>
                        <a><button className="learn-more-btn btn btn-success" href="#">Learn More</button></a>
                    </AuctionInfoBox>
                </CarouselItem>
    
                <CarouselItem>
                    <img src="assets/images/Info-Board_1200x1200.jpg" className=" carousel-image d-block w-100" alt="..." />
                    <AuctionInfoBox >
                        <h1>Auctions Are Coming As Soon As 2021</h1>
                        <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Provident nobis recusandae est aspernatur, ratione explicabo laborum ex neque corporis totam.</p>
                        <a><button className="learn-more-btn btn btn-success" href="#">Learn More</button></a>
                    </AuctionInfoBox>
                </CarouselItem>
            </div>
            <a className="carousel-control-prev" href="#auctionsCarousel" role="button" data-slide="prev">
                <FontAwesomeIcon
                    className="coins-icon"
                    icon={faChevronCircleLeft}
                    size="7x"
                    color="black"
                />
            </a>
            <a class="carousel-control-next" href="#auctionsCarousel" role="button" data-slide="next">
                <FontAwesomeIcon
                    className="coins-icon"
                    icon={faChevronCircleRight}
                    size="7x"
                    color="black"
                />
            </a>
        </div>
    );
};


export default Carousel;